	module.exports = {"data":{"markdownRemark":{"html":"<p>To the national song and dance food city, the characteristic is the performance of folk songs and dances, while eating and watching performances, like the life of ancient emperors. The most lively show is to invite invited guests to go on stage, accept Hada, then dance together, and so on. There is an atmosphere.</p>\n<p>This place was originally the rehearsal hall of the National Song and Dance Troupe. Now it has become a restaurant. There is a lot of space in front of it, and it is probably possible to rehearse. This is undoubtedly a phenomenon of the tide of the market economy. The rehearsal hall of a song and dance troupe has become a restaurant, and it is also a typical symbol of the development of Chinese society. However, it is hard to say that it is an upward development or a kind of development.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0119.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0119.md","frontmatter":{"title":"July 8th Friday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0119.md absPath of file >>> MarkdownRemark"}}

